import React, { Fragment, useEffect } from "react";
import "./ProductDetails.css";
import { useSelector, useDispatch } from "react-redux";
import {
  clearErrors,
  getProductDetails,
} from "../../actions/productAction";
import Loader from "../layout/Loader/Loader";
import { useAlert } from "react-alert";
import MetaData from "../layout/MetaData";

const ProductDetails = ({ match }) => {
  const dispatch = useDispatch();
  const alert = useAlert();

  const { product, loading, error } = useSelector(
    (state) => state.productDetails
  );

  
  useEffect(() => {
    if (error) {
      alert.error(error);
      dispatch(clearErrors());
    }

    dispatch(getProductDetails(match.params.id));
  }, [dispatch, match.params.id, error, alert]);

  return (
    <Fragment>
      {loading ? (
        <Loader />
      ) : (
        <Fragment>
          <MetaData title={`${product.name} -- PORTFOLIO`} />
          <div className="ProductDetails">
          {product && product.images && product.images[1] && (
  <div>
    <img
      className="CarouselImage"
      src={product.images[1].url}
      alt=""
    />
  </div>
)}

            <div>
              <div className="detailsBlock-1">
                <h2>{product.name}</h2>
              </div>
            </div>
          </div>

          <h3 className="reviewsHeading">{product.description}</h3>
          {product && product.images && product.images[2] && (
          <img className="bann" src={product.images[2].url} alt=""/>
          )}

        </Fragment>
      )}
    </Fragment>
  );
};

export default ProductDetails;
